import React from 'react'

import Typography from '@material-ui/core/Typography'
import { withStyles } from '@material-ui/core'

import SEO from '../seo'

import { StyledLayout } from './new-layout'

const styles = theme => ({
  banner: {
    backgroundColor: theme.palette.primary.dark,
    paddingTop: 40,
    paddingBottom: 60,
  },
  title: {
    color: theme.palette.primary.contrastText,
    // marginTop: 20,
  },
  container: {
    padding: 20,
  },
})

const PostLayout = ({
  children,
  classes,
  pageContext: {
    frontmatter: { title, description, keywords },
  },
}) => {
  const bannerRender = (
    <div className={classes.banner}>
      <Typography variant="h4" gutterBottom className={classes.title}>
        {title}
      </Typography>
    </div>
  )

  return (
    <StyledLayout banner={bannerRender}>
      <SEO title={title} keywords={keywords} description={description} />
      <div className={`markdown-body ${classes.container}`}>{children}</div>
    </StyledLayout>
  )
}

export default withStyles(styles)(PostLayout)
